  /**
   * è°èµ·å¾®ä¿¡æ¯ä»
   * Tip
   * éç½®å®å¨æ¯ä»åå
   * jssdk éç½®
   *
   * @see <a href="https://pay.weixin.qq.com/wiki/doc/api/jsapi.php?chapter=7_7&index=6">ç½é¡µç«¯è°èµ·æ¯ä»API</a>
   */
  static pay(param: {
    timeStamp: string, // æ¯ä»ç­¾åæ¶é´æ³ï¼æ³¨æå¾®ä¿¡jssdkä¸­çææä½¿ç¨timestampå­æ®µåä¸ºå°åãä½ææ°ççæ¯ä»åå°çæç­¾åä½¿ç¨çtimeStampå­æ®µåéå¤§åå¶ä¸­çSå­ç¬¦
    timestamp?: string,
    nonceStr: string, // æ¯ä»ç­¾åéæºä¸²ï¼ä¸é¿äº 32 ä½
    package: string, // ç»ä¸æ¯ä»æ¥å£è¿åçprepay_idåæ°å¼ï¼æäº¤æ ¼å¼å¦ï¼prepay_id=***ï¼
    signType: string, // ç­¾åæ¹å¼ï¼é»è®¤ä¸º'SHA1'ï¼ä½¿ç¨æ°çæ¯ä»éä¼ å¥'MD5'
    paySign: string, // æ¯ä»ç­¾å
    success?: any,
    fail?: any
  }): Promise<any>
  {
    param.timestamp = param.timestamp || param.timeStamp;

    const callPay = new Promise((resolve, reject) => {
      param.success = res => {
        if (res.errMsg === 'chooseWXPay:ok' || res.err_msg === 'get_brand_wcpay_request:ok') {
          resolve('success');
        } else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
          reject({code: 1, message: 'åæ¶æ¯ä»'});
        } else if (res.err_msg === 'get_brand_wcpay_request:failed') {
          reject({code: 2, message: 'æ¯ä»å¤±è´¥'});
        } else if (res.err_code === 3) {
          reject({code: 3, message: res});
        } else {
          reject({code: 9, message: res.errMsg || res.err_msg});
        }
      };

      param.fail = res => {
        if (res.errMsg) {
          reject({code: 9, message: res.errMsg || res.err_msg});
        } else {
          reject(res);
        }
      };
      // @deprecated ä½¿ç¨ {@link payOld}, jssdk ç wx.chooseWxpay
      // wx.chooseWXPay(param);
      Wechat.payOld(param);
    });
    return callPay;

    // return Wechat.sign().then(() => callPay);
  }

  private static payOld(sign: {
    appId?: string,
    timeStamp: string,    // æ¯ä»ç­¾åæ¶é´æ³ï¼æ³¨æå¾®ä¿¡jssdkä¸­çææä½¿ç¨timestampå­æ®µåä¸ºå°åãä½ææ°ççæ¯ä»åå°çæç­¾åä½¿ç¨çtimeStampå­æ®µåéå¤§åå¶ä¸­çSå­ç¬¦
    timestamp?: string,
    nonceStr: string,     // æ¯ä»ç­¾åéæºä¸²ï¼ä¸é¿äº 32 ä½
    package: string,      // ç»ä¸æ¯ä»æ¥å£è¿åçprepay_idåæ°å¼ï¼æäº¤æ ¼å¼å¦ï¼prepay_id=***ï¼
    signType: string,     // ç­¾åæ¹å¼ï¼é»è®¤ä¸º'SHA1'ï¼ä½¿ç¨æ°çæ¯ä»éä¼ å¥'MD5'
    paySign: string,      // æ¯ä»ç­¾å
    success?: any,
    fail?: any
  })
  {
    const onBridgeReady = () => WeixinJSBridge.invoke('getBrandWCPayRequest', sign, sign.success);
    if (typeof WeixinJSBridge === 'undefined') {
      if (document.addEventListener) {
        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
      } else if ((document as any).attachEvent) {
        (document as any).attachEvent('WeixinJSBridgeReady', onBridgeReady);
        (document as any).attachEvent('onWeixinJSBridgeReady', onBridgeReady);
      }
    } else {
      onBridgeReady();
    }
  }

  static shareUrl(param: object): string
  {
    let url = location.href.split('?')[0];
    Object.keys(param).forEach((key, index) => {

      if (!param[key] || param[key] === 'null') {
        return;
      }
      url += url.indexOf('?') === -1 ? '?' : '&';
      url += key + '=' + decodeURIComponent(param[key]);
    });

    return url;
  }

  // å¾®ä¿¡ç»å½
  static code(options: {
    /** å¬ä¼å· app id */
    appid: string,
    /** å¼æ¾å¹³å° app id */
    component_appid?: string,
    /** åè° ä¸è¦ uri encode */
    redirect_uri?: string,
    scope?: 'snsapi_userinfo' | 'snsapi_base',
    /** ä»£ç, ç¨äºéææååå®ç°ææä¸å¡ */
    proxy?: string
  }): Observable<string>
  {
    return Observable.create(sub => {
      const code = this.getAuthCode();
      if (!code) {
        const op: any = options || {};
        op.scope = op.scope || 'snsapi_userinfo';
        op.state = 'wechat_auth_' + new Date().getTime();
        op.redirect_uri = encodeURIComponent(options.redirect_uri || location.href.split('?')[0]);
        op.response_type = 'code';

        localStorage.setItem(Wechat.COOKIE_KEY_AUTH_STATE, op.state);

        let url = (options.proxy || 'https://open.weixin.qq.com/connect/oauth2/authorize');
        const query = Object.keys(op).map(key => `${key}=${op[key]}`).join('&');
        url += `?${query}#wechat_redirect`;
        window.location.replace(url);
        sub.complete();
      } else {
        sub.next(code);
        sub.complete();
      }
    });
  }

